import React, { useState } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import './SignUpPageHebrew.css'; // Ensure you create a corresponding CSS file for Hebrew styles
import { supabase } from '../../supabaseClient';
import logoicon from '../../assets/icon.png';

// Hebrew to English mapping
const hebrewToEnglishSportMap = {
  '': 'Archery',
  ' ': 'Artistic Gymnastics',
  ' ': 'Artistic Swimming',
  '': 'Athletics',
  '': 'Badminton',
  '': 'Basketball',
  ' 3  3': 'Basketball3vs3',
  ' ': 'Beach Volleyball',
  ' ': 'Canoe Slalom',
  ' ': 'Canoe Sprint',
  ' BMX  ': 'Cycling BMX Freestyle',
  '  BMX': 'Cycling BMX Racing',
  ' ': 'Cycling Mountain Bike',
  ' ': 'Diving',
  '': 'Fencing',
  ' ': 'Field Hockey',
  ' ': 'Flag Football',
  '': 'Football',
  '': 'Handball',
  '': 'Judo',
  '': 'Lacrosse',
  '  ': 'Marathon Swimming',
  ' ': 'Rhythmic Gymnastics',
  ' ': 'Road Cycling',
  '': 'Rowing',
  ' ': 'Rugby Sevens',
  ' 470': 'Sailing 470',
  ' 49er': 'Sailing 49er',
  '  ': 'Sailing Formula Kite',
  ' IQFoil': 'Sailing IQFoil',
  ' ': 'Sailing Laser Class',
  '  17': 'Sailing Nacra17',
  '': 'Sport Climbing',
  ' ': 'Surfing',
  '': 'Swimming',
  '': 'Taekwondo',
  '': 'Tennis',
  ' ': 'Track Cycling',
  '': 'Trampoline',
  '': 'Volleyball',
  '': 'Water Polo',
  '': 'Wrestling',
};

const SignUpPageHebrew = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [language, setLanguage] = useState('Hebrew'); // Default to Hebrew
  const [error, setError] = useState('');
  const navigate = useNavigate();

  const handleSignUp = async (e) => {
    e.preventDefault();

    try {
      // Sign up the user
      const { user, error: signUpError } = await supabase.auth.signUp({
        email,
        password,
      });

      if (signUpError) {
        setError(signUpError.message);
        console.error('Sign-up error:', signUpError);
        return;
      }

      if (!user) {
        setError('  ,    .');
        console.error('User data is missing after sign-up.');
        return;
      }

      // Navigate to the settings page after successful sign-up
      navigate('/he/user-settings');
    } catch (error) {
      setError('   .');
      console.error('Unexpected error during sign-up:', error);
    }
  };

  return (
    <div className="sign-up-page" dir="rtl">
      <header className="app-bar">
        <div className="app-bar-content">
          <Link to="/he" className="home-button">
            <img src={logoicon} alt=" " className="home-icon" />
          </Link>
          <nav className="nav-links">
            <div className="dropdown">
              <Link to="/he/sports" className="dropbtn-link">
                <button className="dropbtn"></button>
              </Link>
              <div className="dropdown-content">
                {Object.keys(hebrewToEnglishSportMap).map(sport => (
                  <Link key={sport} to={`/he/sport/${hebrewToEnglishSportMap[sport].toLowerCase().replace(/\s+/g, '-')}`}>{sport}</Link>
                ))}
              </div>
            </div>
            <Link to="/he/calendar" className="calendar-link"> </Link>
            <Link to="/he/user-settings" className="settings-link"></Link>
          </nav>
          <button onClick={() => navigate('/signup')} className="toggle-language-button-eng">
            English
          </button>
        </div>
      </header>

      <div className="sign-up-container">
        <h2></h2>
        {error && <p className="error">{error}</p>}
        <form onSubmit={handleSignUp}>
          <input
            type="email"
            placeholder=""
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
          />
          <input
            type="password"
            placeholder=""
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
          <div className="language-select">
            <label htmlFor="language"> :</label>
            <select
              id="language"
              value={language}
              onChange={(e) => setLanguage(e.target.value)}
            >
              <option value="English"></option>
              <option value="Hebrew"></option>
            </select>
          </div>
          <button type="submit"></button>
        </form>
        <p>   ? <a href="/he/login"></a></p>
      </div>
    </div>
  );
};

export default SignUpPageHebrew;
