import React, { useState, useEffect } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import './LoginPageHebrew.css';
import { supabase } from '../../supabaseClient';
import logoicon from '../../assets/icon.png';

// Hebrew to English mapping
const hebrewToEnglishSportMap = {
  '': 'Archery',
  ' ': 'Artistic Gymnastics',
  ' ': 'Artistic Swimming',
  '': 'Athletics',
  '': 'Badminton',
  '': 'Basketball',
  ' 3  3': 'Basketball3vs3',
  ' ': 'Beach Volleyball',
  ' ': 'Canoe Slalom',
  ' ': 'Canoe Sprint',
  ' BMX  ': 'Cycling BMX Freestyle',
  '  BMX': 'Cycling BMX Racing',
  ' ': 'Cycling Mountain Bike',
  ' ': 'Diving',
  '': 'Fencing',
  ' ': 'Field Hockey',
  ' ': 'Flag Football',
  '': 'Football',
  '': 'Handball',
  '': 'Judo',
  '': 'Lacrosse',
  '  ': 'Marathon Swimming',
  ' ': 'Rhythmic Gymnastics',
  ' ': 'Road Cycling',
  '': 'Rowing',
  ' ': 'Rugby Sevens',
  ' 470': 'Sailing 470',
  ' 49er': 'Sailing 49er',
  '  ': 'Sailing Formula Kite',
  ' IQFoil': 'Sailing IQFoil',
  ' ': 'Sailing Laser Class',
  '  17': 'Sailing Nacra17',
  '': 'Sport Climbing',
  ' ': 'Surfing',
  '': 'Swimming',
  '': 'Taekwondo',
  '': 'Tennis',
  ' ': 'Track Cycling',
  '': 'Trampoline',
  '': 'Volleyball',
  '': 'Water Polo',
  '': 'Wrestling',
};

const LoginPageHebrew = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [availableSports, setAvailableSports] = useState([]);
  const navigate = useNavigate();

  useEffect(() => {
    const fetchSports = async () => {
      const { data, error } = await supabase
        .from('tournaments')
        .select('sport');

      if (error) {
        console.error('Error fetching sports:', error);
      } else {
        const validSports = [...new Set(data.map(item => item.sport))].filter(Boolean).sort();
        // Map the valid sports to their Hebrew names
        const validSportsInHebrew = validSports.map(sport => {
          return Object.keys(hebrewToEnglishSportMap).find(key => hebrewToEnglishSportMap[key] === sport);
        }).filter(Boolean);
        setAvailableSports(validSportsInHebrew);
      }
    };

    fetchSports();  // Fetch available sports for the dropdown
  }, []);

  const handleLogin = async (e) => {
    e.preventDefault();

    const { error } = await supabase.auth.signInWithPassword({ email, password });

    if (error) {
      setError(error.message);
    } else {
      navigate('/he/user-settings'); // Redirect to settings page on successful login
    }
  };

  return (
    <div className="login-page" dir="rtl">
      <header className="app-bar">
        <div className="app-bar-content">
          <Link to="/he" className="home-button">
            <img src={logoicon} alt=" " className="home-icon" />
          </Link>
          <nav className="nav-links">
            <div className="dropdown">
              <Link to="/he/sports" className="dropbtn-link">
                <button className="dropbtn"></button>
              </Link>
              <div className="dropdown-content">
                {availableSports.map(sport => (
                  <Link key={sport} to={`/he/sport/${hebrewToEnglishSportMap[sport].toLowerCase().replace(/\s+/g, '-')}`}>{sport}</Link>
                ))}
              </div>
            </div>
            <Link to="/he/calendar" className="calendar-link"> </Link>
            <Link to="/he/user-settings" className="settings-link"></Link>
          </nav>
          <button onClick={() => navigate('/login')} className="toggle-language-button-eng">
            English
          </button>
        </div>
      </header>

      <div className="login-container">
        <h2></h2>
        {error && <p className="error">{error}</p>}
        <form onSubmit={handleLogin}>
          <input
            type="email"
            placeholder=""
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
          />
          <input
            type="password"
            placeholder=""
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
          <button type="submit"></button>
        </form>
        <p>  ? <a href="/he/signup"></a></p>
      </div>
    </div>
  );
};

export default LoginPageHebrew;
